.gamburger {
  position: relative;
  bottom: 5px;
  left: 0;
  display: block;
  cursor: pointer;

  span {
    position: relative;
    left: 4px;
    width: 20px;
    height: 1px;
    background-color: #767676;
    display: inline-block;
    cursor: pointer;

    @media (min-width: @screen-sm) {
      display: none;
    }

    &:before {
      content: "";
      cursor: pointer;
      position: absolute;
      top: 4px;
      right: 0px;
      width: 20px;
      height: 1px;
      background-color: #767676;
    }

    &:after {
      content: "";
      position: absolute;
      cursor: pointer;
      top: 8px;
      right: 0px;
      width: 20px;
      height: 1.4px;
      background-color: #767676;
    }
  }
}

.main-menu {
  display: none;
  list-style-type: none;    
  padding: 0;
  order:1;
  width: 100%;
  
