        margin-bottom: 14px;
      }
    }

    &__fruit, &__bag {
      margin-bottom: 30px;
    }

    &__title-pages {
      font-size: 14px;
      font-family: @font-text;
      text-transform: uppercase;
      width: 100%;
      text-align: left;
      padding-right: 9px;
      letter-spacing: 2px;
      line-height: 1.4;
      margin-bottom: 17px;
      @media (min-width: @screen-sm) {
        font-size: 18px;
      }
    }

    &__pop-btn {
      color: #648cff;
      text-align: left;
      font-size: 14px;
      text-decoration: none;
      border-bottom: 1px solid #648cff;
      width: 119px;
      padding-bottom: 4px;

      @media (min-width: @screen-sm) {
        font-size: 16px;
      }
    }

    &__inner {
      width: 250px;
      margin: 0 auto;

    @media (min-width: @screen-sm) {
      width: 580px;

      p {
        width: 380px;
        text-align: left;
        margin: 0;
        font-size: 18px;
        padding-bottom: 225px;
      }
    }

    @media (min-width: @screen-lg) {
      width: 1016px;

      p {
        width: 586px;
        font-size: 24px;
        padding-bottom: 398px;
      }
    }
    }

  &__description {
    
   
  }

  p {
      width: 246px;
      text-align: left;
      margin: 0 auto;
      padding: 35px 0 40px;
      text-transform: none;
      font-size: 14px;
      line-height: 1.5;
    }

    &__about p {
      padding-bottom: 0;
      color: #666;
      @media (min-width: @screen-sm) {
        margin: 0;
        width: 465px;
        font-size: 18px;
      }
    }

    &__block {
      display: flex;
      flex-direction: column;
      padding: 8px 0 0 10px;

      @media (min-width: @screen-lg) {
        width: 415px;
      }
